@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
  overflow-y: auto;
  overflow-x: hidden;
  height: 100vh;
  scroll-behavior: smooth;
}

.App > section {
  width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
}

#home {
  display: flex;
  background-color: #eff8f8;
  flex-wrap: wrap;
}

.home__left {
  /* background-color: #447272; */
  width: 50%;
}

.imgContainer {
  width: 100%;
  height: 100%;
  background-image: url(/about_tezar/static/media/PasFoto-min.95cca055.png);
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: grayscale();
          filter: grayscale();
}

.home__right {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.name {
  margin-left: 1rem;
  font-size: 48px;
  margin-bottom: 1.5rem;
}

.name > p {
  font-size: 24px;
  color: #1fbbb0;
}

.about {
  width: 60%;
  text-align: left;
  margin-left: 1rem;
}

.about > h3 {
  margin-bottom: 1rem;
}

#education,
#contact {
  display: flex;
  align-items: center;
  justify-content: center;
}

#skills {
  display: flex;
}

.skills__left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.skills__right {
  display: flex;
  width: 50%;
}

.swipe_container {
  width: 80%;
}

::-webkit-scrollbar {
  display: none;
}

.header {
  display: flex;
  position: fixed;
  width: 100vw;
  height: 50px;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  z-index: 100;
  transition: 0.5s;
}

.header__menu {
  display: flex;
  justify-content: center;
}

.header__menu > a {
  text-decoration: none;
  margin: 1rem;
  color: #1fbbb0;
  font-weight: 400;
  transition: 0.3s;
}

.header__menu > a:hover {
  font-size: 1.3rem;
}

